* {
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  font-weight: bold;
  font-size: 7vw;
}
h2{
  text-align: center;
  font-size: 4vw;
  padding-top: 20vh;
  
}
h3{
  text-align: center;
  font-size: 3vw;
  padding: 5vh;
}
